
.envelope{
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-column-gap: 0px;
    grid-row-gap: 50px;
    margin-top: 200px;
   
}
.text{
    font-family: 'Raleway', sans-serif;   
    /* font-weight: bold; */
    text-shadow: 1px 1px 0px #b0a7a7;
    font-size: 100px;
    border: 1px solid black;
    width: 50%;
    text-align: center;
    margin:auto;
    background-color:rgba(251, 243, 243, 0.501); 
    
}
.project1{
    height: 500px;
    width: 600px;
    margin-left: 80px;
    margin-bottom: 200px;
    padding: 50px;
    border: 1px solid black;
    background-image: url('https://i.pinimg.com/564x/47/e4/8f/47e48f9587490b257304f20bfcdd3950.jpg');
}
.description1{
    font-family: 'Raleway', sans-serif;   
    font-size:30px;
    text-align: center;
    margin-left: 80px;
    position: relative;
    left: 1000px;
    text-align: center;
    margin:auto;
    margin-top: -555px;
    margin-bottom: 60px;  
}
.github1{
    background-color: rgba(243, 243, 243, 0.862);   
    font-weight: bold;
    left: 20px;
    text-align: center;
    margin-top: 150px;
}
